import { useState, useEffect } from 'react';
import './ProductButton.css';

const iniState = {
    isActive: false
}

const ProductButton = (type, name, link, ind, changeBunttonState, currentActiveButton) => {
    const [buttonState, setButtonActive] = useState(iniState);

    const handleClick = (name) => {
        setButtonActive({...buttonState, isActive: !buttonState.isActive})
        changeBunttonState(name)
    }

    
    useEffect(() => {
        console.log('buttonName:', name, 'buttonState', buttonState)
        if(name != currentActiveButton) {
            setButtonActive(iniState)
        }
    }, [currentActiveButton])
    
    return (
        <div 
            className={buttonState.isActive ? `${type}ButtonActive` : `${type}Button`}
            key={ind}
            style={{
                backgroundImage: "url("+link.default+")",
                backgroundSize: '100%',
                backgroundRepeat: 'no-repeat',
                backgroundPosition:'center'
            }}
            onClick={ () => handleClick(name) }
        >
            { type === 'family' && <p id='familyButtonLabel'>{name}</p> }
            { type === 'canopy' && <p id='canopyButtonLabel'>{name}</p> }
            { type === 'preconfig' && <p id='preconfigButtonLabel'>{name}</p> }
        </div>
    )
};
export default ProductButton;