import { useState, useEffect } from 'react';

import './PreconfigurationGallery.css';

import ProductButton from '../ProductButton/ProductButton';


const iniState = {
    isActive: false
}

const FinishButton = (name, link, ind, changeButtonState, currentActiveButton) => {
    const [buttonState, setButtonActive] = useState(iniState);
    
    const handleClick = (name) => {
        setButtonActive({...buttonState, isActive: !buttonState.isActive});
        changeButtonState(name);
    };

    
    useEffect(() => {
        console.log('buttonName:', name, 'buttonState', buttonState)
        if(name != currentActiveButton) {
            setButtonActive(iniState)
        }
    }, [currentActiveButton])
    return (
        <div>
            <div className={buttonState.isActive ? `canopyFinishButtonActive` : `canopyFinishButton`}
            key={ind}
            style={{
                backgroundColor: link
            }}
            onClick={ () => handleClick(name) }>

            </div>
            <p id='canopyFinishButtonLabel'>{name}</p>
        </div>
    )
}

const PreconfigurationGallery = (props) => {
    const { steps, input, currentStepID, currentFamily, selectPreconfiguration } = props;
   
    const [currentActiveButton, setButtonActive] = useState('');

    const changeButtonState = (name) => {
        // selectDimension(name)
        console.log('I am from preconfiguration gallery:', name)
        setButtonActive(name)
    }
    
    
    return(
        <div className='preconfigurationGalleryContainer'>

            <div className='ceilingHeightMenu'>
                <p className='menuLabel'>Altezza soffitto</p>
                <img id='ceilingHeightImg' src={require('../../images/preconfig/soffitto.png').default}></img>
            </div>

            <div className='preconfigurationMenu'>
                <p className='menuLabel'>Preconfigurazione</p>
                <div className='preconfigButtonsContainer'>
                    {
                        input.filter(i => i.name === currentFamily)[0].preconfigs
                        .map( (i, _i) => ProductButton('preconfig', i.name, i.imgUrl, _i, changeButtonState, currentActiveButton))
                    }
                </div>
            </div>

            <div className='lampFinishMenu'>
                <p className='menuLabel'>Finiture lampade</p>
                <div className='lampFinishButtonsContainer'>
                    {
                        input.filter(i => i.name === currentFamily)[0].finishes
                        .map( (i, _i) => FinishButton(i.name, i.imgUrl, _i, changeButtonState, currentActiveButton))
                    }
                </div>
            </div>



           
        </div>
    )
};

export default PreconfigurationGallery;