import { useState } from 'react';

import './FamilyGallery.css';
import ProductButton from '../ProductButton/ProductButton';

const FamilyGallery = (props) => {
    const { steps, input, currentStepID, currentFamily, selectFamily } = props;
    console.log('current step ID is:', currentStepID)
    
    const [currentActiveButton, setButtonActive] = useState('');

    const changeButtonState = (name) => {
        selectFamily(name)
        console.log('I am from family gallery:', name)
        setButtonActive(name)
    }

    return(
        <div className='familyGallery'>

            <p id='stepName'>{ steps.filter(i => i.stepID === currentStepID)[0].name }</p>
            <p id='stepDescription'>{ steps.filter(i => i.stepID === currentStepID)[0].description }</p>
            
            <div className='familyMenuContainer'>
                { input.map( (i, _i) => ProductButton('family', i.name, i.link, _i, changeButtonState, currentActiveButton))}
            </div>
        
        </div>
    )
}

export default FamilyGallery;