import { useState } from 'react';
import './CanopyGallery.css'

import ProductButton from '../ProductButton/ProductButton';

const CanopyGallery = (props) => {
    const {steps, input, currentStepID, currentCanopy, selectCanopy} = props;
   
    const [currentActiveButton, setButtonActive] = useState('');

    const changeButtonState = (name) => {
        selectCanopy(name)
        console.log('I am from canopy gallery:', name)
        setButtonActive(name)
    }

    return(
        <div>
            <p id='stepName'>{ steps.filter(i => i.stepID === currentStepID)[0].name }</p>
            <p id='stepDescription'>{ steps.filter(i => i.stepID === currentStepID)[0].description }</p>
            <div className='canopyMenuContainer' >
                <div>
                    <p>{input[0].name}</p>
                    <div className='singleCanopyContainer'>
                        {input[0].canopies.map( (i, _i) => ProductButton('canopy', i.name, i.link, _i, changeButtonState, currentActiveButton))}
                        
                    </div>
                </div>
                <div>
                    <p>{input[1].name}</p>
                    <div className='multipleCanopyContainer'>
                        { input[1].canopies.map( (i, _i) => ProductButton('canopy', i.name, i.link, _i, changeButtonState, currentActiveButton)) }
                    </div>
                </div>
            </div> 
        </div>
    )
}

export default CanopyGallery;