import './LangMenu.css';

const langList = ['Italiano', 'English', 'Français', 'Deutsch', 'Español', 'Русский', 'Meh'];

const LangButton = (item) => {
    return (
        <div className='langButton'>
            {item}
        </div>
    )
}

const LangMenu = () => {
    return (
        <div className='langList'>
            { langList.map((i) => {
                return (
                    LangButton(i)
                )
            }) }
        </div>
    )
};

export default LangMenu;