import { useState, useEffect } from 'react';
import './CanopyDimensionGallery.css';

const iniState = {
    isActive: false
}

const DimensionButton = (name, link, ind, changeButtonState, currentActiveButton, getImageUrl) => {
    
    const [buttonState, setButtonActive] = useState(iniState);
    const handleClick = (name) => {
        setButtonActive({...buttonState, isActive: !buttonState.isActive});
        changeButtonState(name);
        getImageUrl(link);
    };

    
    useEffect(() => {
        console.log('buttonName:', name, 'buttonState', buttonState)
        if(name != currentActiveButton) {
            setButtonActive(iniState)
        }
    }, [currentActiveButton])
    
    return (
        <div 
            className={buttonState.isActive ? `canopyDimensionButtonActive` : `canopyDimensionButton`}
            key={ind}
            onClick={ () => handleClick(name) }
        >
            { <p id='canopyDimensionButtonLabel'>{name}</p> }
        </div>
    )
};

const FinishButton = (name, link, ind, changeButtonState, currentActiveButton) => {
    const [buttonState, setButtonActive] = useState(iniState);
    
    const handleClick = (name) => {
        setButtonActive({...buttonState, isActive: !buttonState.isActive});
        changeButtonState(name);
    };

    
    useEffect(() => {
        console.log('buttonName:', name, 'buttonState', buttonState)
        if(name != currentActiveButton) {
            setButtonActive(iniState)
        }
    }, [currentActiveButton])
    return (
        <div>
            <div className={buttonState.isActive ? `canopyFinishButtonActive` : `canopyFinishButton`}
            key={ind}
            style={{
                backgroundColor: link
            }}
            onClick={ () => handleClick(name) }>

            </div>
            <p id='canopyFinishButtonLabel'>{name}</p>
        </div>
    )
}
const CanopyDimensionGallery = (props) => {
    const {steps, input, currentStepID, currentCanopyType, currentCanopyName, selectDimension} = props;
   
    const [currentActiveButton, setButtonActive] = useState('');
    const [imgUrl, setImageUrl] = useState('');

    const changeButtonState = (name) => {
        selectDimension(name)
        console.log('I am from canopy dimension gallery:', name)
        setButtonActive(name)
    }
    const getImageUrl = (link) => {
        setImageUrl(link);
    };
    
    return(
        <div>
            <p id='stepName'>{ steps.filter(i => i.stepID === currentStepID)[0].name }</p>
            <p id='stepDescription'>{ steps.filter(i => i.stepID === currentStepID)[0].description }</p>
            <div className='canopyDimensionMenuContainer' >
                <div className='canopyDimensionButtonsContainer'>
                    {
                        input.filter(i => i.type === currentCanopyType)[0].canopies
                        .filter( i => i.name === currentCanopyName)[0].dimensions
                        .map( (i, _i)  => DimensionButton(i.name, i.imgUrl, _i, changeButtonState, currentActiveButton, getImageUrl))
                    } 
                </div>
                <div className='canopyDimensionImageContainer'
                    style={{
                        backgroundImage: "url("+imgUrl.default+")",
                        backgroundSize: '60%',
                        backgroundRepeat: 'no-repeat',
                        backgroundPosition:'center'
                    }}
                ></div>
                <div className='canopyFinishButtonsContainer'>
                    {
                        input.filter(i => i.type === currentCanopyType)[0].canopies
                        .filter( i => i.name === currentCanopyName)[0].finishes
                        .map( (i, _i)  => FinishButton(i.name, i.imgUrl, _i, changeButtonState, currentActiveButton))
                    }
                    
                </div>
            </div> 
        </div>
    )
}

export default CanopyDimensionGallery;

