import './StepBar.css'


const Button = (i, currentStepID) => {
    let style;
    currentStepID === i.stepID ? style = 'buttonTickActive' : style='buttonTick';
    return (
        <div className='stepButton'>
            <div className={style}></div>
            <p className='buttonLabel'>{i.name}</p>
        </div>
    )
};

const StepBar = (props) => {
    const { steps, currentStepID } = props;
    return(
        <div className='stepBar'>
            {steps.map(i => {
                return(
                    Button (i, currentStepID)
                )  
            })}
        </div>
    )
}

export default StepBar;