import React, {useState} from 'react';

import './App.css';
import logo from './images/Lodes-Logo.png';
import langlogo from './images/langlogo.png';

import LangMenu from './components/LangMenu/LangMenu';
import PathMenu from './components/PathMenu/PathMenu';

const iniState = {
  isLangMenuOpen: false,
  currentStep: 'Step1'
}





function App() {
  const [appState, setAppState] = useState(iniState);

  const openLangMenu = () => {
    setAppState({...appState, isLangMenuOpen: !appState.isLangMenuOpen})
  }

  

  return (
    <div className="App">
      <header className="header">
        <img className='logoLodes' src={logo} alt='lodes logo'></img>
        <div className='langMenu'
          onClick={ () => openLangMenu() }
        >
          <p id='lang'>it</p>
          <img id='langLogo' src={ langlogo } alt='world logo'></img>
          {appState.isLangMenuOpen ? <LangMenu /> : null}
        </div>
      </header>
      <div id='intro'>
        <p id='introTitle'>Lodes configuratore</p>
        <p id='introNotes'>
          Prima di procedere alla configurazione, dovrai solo prendere delle semplici decisioni. <br/> 
          Ricorda che potrai cambiare idea in qualsiasi momento.
        </p>
      </div>
      <div className='configContainer'>
        <PathMenu />
      </div>

      

      <footer className='footer'>
        <p>© 2021 Lodes S.r.l</p>
      </footer>
    </div>
  );
}

export default App;
